import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
import img1 from "../img/contemplative-reptile.jpg"
import googles from '../img/googles.jpg'
import classroom from '../img/classroom..JPG'
const list = [
    {
        header: 'Header',
        description: 'description',
        image: googles
    },
    {
        header: 'Header',
        description: 'description',
        image: classroom
    },
    {
        header: 'Header',
        description: 'description',
        image: img1
    },
]

export default function ActionAreaCardImage() {
    return (
        list.map(({header,description,image}, key )=>{
            return (
                <Card sx={{ width: 345, marginTop: 5 }} key={key}>
                    <CardActionArea>
                        <CardMedia
                            component="img"
                            height="140"
                            src={image}
                            alt="green iguana"
                        />
                        <CardContent>
                            <Typography gutterBottom variant="h5" component="div">
                                {header}
                            </Typography>
                            <Typography variant="body2" color="text.secondary">
                                {description}
                            </Typography>
                        </CardContent>
                    </CardActionArea>
                </Card>
            )
        })

    );
}